import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';

/**
 * Generated class for the RoofsheetPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-roofsheet',
  templateUrl: 'roofsheet.html',
})
export class RoofsheetPage {

        _imageViewerCtrl: ImageViewerController;
 
  constructor(public navCtrl: NavController, imageViewerCtrl: ImageViewerController, public navParams: NavParams, public platform: Platform) {
    this._imageViewerCtrl = imageViewerCtrl;
     platform.registerBackButtonAction(() => {
      console.log("backPressed 1");
    },1);
  }
 
  presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }
  
  
  

  ionViewDidLoad() {
    console.log('ionViewDidLoad RoofsheetPage');
  }
  
         startApp(page) {
    this.navCtrl.setRoot(page, {}, {
      animate: true,
      direction: 'forward'
    });
  
}

}



